import React, { PureComponent } from 'react';
import classNames from 'classnames';
import '../style/index';
import './index.css';
export default class InputItem extends PureComponent {
    render() {
        const { prefixCls, value, placeholder, size, disabled, noBorder, noRadius, handleChange, className, style } = this.props;
        const inputClassNames = classNames(prefixCls, className, {
            [`${prefixCls}-${size}`]: size,
            [`${prefixCls}-disabled`]: disabled,
            [`${prefixCls}-noborder`]: noBorder,
            [`${prefixCls}-noradius`]: noRadius
        });
        return (<input type="text" className={inputClassNames} style={style} value={value} placeholder={placeholder} onChange={handleChange}/>);
    }
}
InputItem.defaultProps = {
    prefixCls: 'zzc-input-item',
    value: '',
    placeholder: '说点什么...',
    size: false,
    disabled: false,
    noBorder: false,
    noRadius: false,
    handleChange() { },
    className: '',
    style: {}
};



